"use client"

import { useState, use } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { useRouter } from "next/navigation"
import { 
  ArrowLeft, 
  Phone, 
  MapPin, 
  Truck,
  CheckCircle,
  XCircle,
  Plus,
  Minus,
  Loader2
} from "lucide-react"
import { formatPaise, formatQuantity, CUSTOMER_STATUS_LABELS, DELIVERY_STATUS_LABELS } from "@/lib/constants"
import { formatDate, formatPhone } from "@/lib/utils"
import toast from "react-hot-toast"

function PageSkeleton() {
  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        <div className="flex items-center gap-4">
          <div className="w-10 h-10 bg-gray-200 rounded-xl animate-pulse" />
          <div className="flex-1">
            <div className="h-8 w-48 bg-gray-200 rounded animate-pulse" />
            <div className="h-4 w-32 bg-gray-200 rounded mt-2 animate-pulse" />
          </div>
        </div>
        <div className="grid lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-6">
            <div className="h-64 bg-white rounded-2xl animate-pulse" />
            <div className="h-48 bg-white rounded-2xl animate-pulse" />
          </div>
          <div className="space-y-6">
            <div className="h-48 bg-white rounded-2xl animate-pulse" />
            <div className="h-36 bg-white rounded-2xl animate-pulse" />
          </div>
        </div>
      </div>
    </div>
  )
}

export default function CustomerDetailPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = use(params)
  const router = useRouter()
  const queryClient = useQueryClient()
  const [showApproval, setShowApproval] = useState(false)
  const [showWalletAdjust, setShowWalletAdjust] = useState(false)
  const [selectedDeliveryPerson, setSelectedDeliveryPerson] = useState("")
  const [walletAmount, setWalletAmount] = useState("")
  const [walletDescription, setWalletDescription] = useState("")
  const [walletType, setWalletType] = useState<"ADMIN_CREDIT" | "ADMIN_DEBIT">("ADMIN_CREDIT")

  const { data, isLoading } = useQuery({
    queryKey: ["admin-customer", id],
    queryFn: async () => {
      const res = await fetch(`/api/admin/customers/${id}`)
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const { data: deliveryPersonsData } = useQuery({
    queryKey: ["delivery-persons"],
    queryFn: async () => {
      const res = await fetch("/api/admin/delivery-persons")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const approveMutation = useMutation({
    mutationFn: async (approved: boolean) => {
      const res = await fetch(`/api/admin/customers/${id}/approve`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          approved,
          deliveryPersonId: selectedDeliveryPerson,
        }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Customer updated")
      queryClient.invalidateQueries({ queryKey: ["admin-customer", id] })
      setShowApproval(false)
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const walletMutation = useMutation({
    mutationFn: async () => {
      const res = await fetch(`/api/admin/customers/${id}/wallet`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          amountPaise: parseInt(walletAmount) * 100,
          description: walletDescription,
          type: walletType,
        }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Wallet updated")
      queryClient.invalidateQueries({ queryKey: ["admin-customer", id] })
      setShowWalletAdjust(false)
      setWalletAmount("")
      setWalletDescription("")
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  if (isLoading) return <PageSkeleton />

  const customer = data?.data?.customer
  const bottleBalance = data?.data?.bottleBalance
  const deliveryPersons = deliveryPersonsData?.data?.deliveryPersons || []

  if (!customer) {
    return (
      <div className="p-6 lg:p-8">
        <div className="max-w-7xl mx-auto">
          <div className="bg-white rounded-2xl border border-gray-200 shadow-sm p-12 text-center">
            <p className="text-gray-500">Customer not found</p>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        
        {/* Header */}
        <div className="flex items-center gap-4">
          <button
            onClick={() => router.back()}
            className="w-10 h-10 flex items-center justify-center rounded-xl bg-gray-100 hover:bg-gray-200 transition-colors"
          >
            <ArrowLeft className="w-5 h-5 text-gray-600" />
          </button>
          <div className="flex-1">
            <h1 className="text-2xl font-bold text-gray-900">{customer.name}</h1>
            <p className="text-gray-500">{customer.email}</p>
          </div>
          <span className={`inline-flex items-center px-3 py-1.5 rounded-full text-sm font-semibold ${
            customer.status === "ACTIVE" 
              ? "bg-emerald-100 text-emerald-800"
              : customer.status === "PENDING_APPROVAL"
              ? "bg-amber-100 text-amber-800"
              : customer.status === "BLOCKED"
              ? "bg-red-100 text-red-800"
              : "bg-gray-100 text-gray-800"
          }`}>
            {CUSTOMER_STATUS_LABELS[customer.status as keyof typeof CUSTOMER_STATUS_LABELS]}
          </span>
        </div>

        {/* Pending Approval Banner */}
        {customer.status === "PENDING_APPROVAL" && (
          <div className="bg-amber-50 border border-amber-200 rounded-2xl p-6">
            <h3 className="text-lg font-semibold text-amber-800 mb-4">Pending Approval</h3>
            
            {!showApproval ? (
              <div className="flex gap-3">
                <button
                  onClick={() => setShowApproval(true)}
                  className="inline-flex items-center gap-2 px-5 py-2.5 bg-emerald-600 text-white font-medium rounded-xl hover:bg-emerald-700 transition-colors"
                >
                  <CheckCircle className="w-4 h-4" />
                  Approve
                </button>
                <button
                  onClick={() => approveMutation.mutate(false)}
                  className="inline-flex items-center gap-2 px-5 py-2.5 border border-red-200 text-red-600 font-medium rounded-xl hover:bg-red-50 transition-colors"
                >
                  <XCircle className="w-4 h-4" />
                  Reject
                </button>
              </div>
            ) : (
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Assign Delivery Person
                  </label>
                  <select
                    value={selectedDeliveryPerson}
                    onChange={(e) => setSelectedDeliveryPerson(e.target.value)}
                    className="w-full h-11 px-4 border border-gray-200 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                  >
                    <option value="">Select delivery person...</option>
                    {deliveryPersons.filter((dp: any) => dp.isActive).map((dp: any) => (
                      <option key={dp.id} value={dp.id}>
                        {dp.name} ({dp.zone || "No zone"})
                      </option>
                    ))}
                  </select>
                </div>
                <div className="flex gap-3">
                  <button
                    onClick={() => approveMutation.mutate(true)}
                    disabled={!selectedDeliveryPerson || approveMutation.isPending}
                    className="inline-flex items-center gap-2 px-5 py-2.5 bg-slate-900 text-white font-medium rounded-xl hover:bg-slate-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  >
                    {approveMutation.isPending && <Loader2 className="w-4 h-4 animate-spin" />}
                    Confirm Approval
                  </button>
                  <button
                    onClick={() => setShowApproval(false)}
                    className="px-5 py-2.5 text-gray-600 font-medium hover:bg-amber-100 rounded-xl transition-colors"
                  >
                    Cancel
                  </button>
                </div>
              </div>
            )}
          </div>
        )}

        {/* Main Content Grid */}
        <div className="grid lg:grid-cols-3 gap-6">
          
          {/* Left Column - Main Info */}
          <div className="lg:col-span-2 space-y-6">
            
            {/* Contact Information */}
            <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
              <div className="px-6 py-5 border-b border-gray-200">
                <h3 className="text-lg font-semibold text-gray-900">Contact Information</h3>
              </div>
              <div className="p-6 space-y-5">
                <div className="flex items-center gap-4">
                  <div className="w-10 h-10 bg-gray-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <Phone className="w-5 h-5 text-gray-500" />
                  </div>
                  <span className="text-gray-900">{formatPhone(customer.phone)}</span>
                </div>
                <div className="flex items-start gap-4">
                  <div className="w-10 h-10 bg-gray-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <MapPin className="w-5 h-5 text-gray-500" />
                  </div>
                  <div className="text-gray-900">
                    <p>{customer.addressLine1}</p>
                    {customer.addressLine2 && <p>{customer.addressLine2}</p>}
                    {customer.landmark && <p className="text-gray-500">Near {customer.landmark}</p>}
                    <p>{customer.city} - {customer.pincode}</p>
                  </div>
                </div>
                {customer.deliveryPerson && (
                  <div className="flex items-center gap-4">
                    <div className="w-10 h-10 bg-blue-100 rounded-xl flex items-center justify-center flex-shrink-0">
                      <Truck className="w-5 h-5 text-blue-600" />
                    </div>
                    <span className="text-gray-900">
                      Assigned to: <strong>{customer.deliveryPerson.name}</strong>
                      {customer.deliveryPerson.zone && ` (${customer.deliveryPerson.zone})`}
                    </span>
                  </div>
                )}
              </div>
            </div>

            {/* Subscription */}
            {customer.subscription && (
              <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
                <div className="px-6 py-5 border-b border-gray-200">
                  <h3 className="text-lg font-semibold text-gray-900">Subscription</h3>
                </div>
                <div className="p-6">
                  <div className="grid grid-cols-2 gap-6">
                    <div>
                      <p className="text-sm text-gray-500 mb-1">Daily Quantity</p>
                      <p className="text-xl font-semibold text-gray-900">{formatQuantity(customer.subscription.dailyQuantityMl)}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-500 mb-1">Daily Price</p>
                      <p className="text-xl font-semibold text-gray-900">{formatPaise(customer.subscription.dailyPricePaise)}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-500 mb-1">Current Cycle</p>
                      <p className="text-xl font-semibold text-gray-900">Month {customer.subscription.paymentCycleCount}</p>
                    </div>
                    <div>
                      <p className="text-sm text-gray-500 mb-1">Status</p>
                      <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-semibold ${
                        customer.subscription.status === "ACTIVE"
                          ? "bg-emerald-100 text-emerald-800"
                          : "bg-gray-100 text-gray-800"
                      }`}>
                        {customer.subscription.status}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/* Recent Deliveries */}
            <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
              <div className="px-6 py-5 border-b border-gray-200">
                <h3 className="text-lg font-semibold text-gray-900">Recent Deliveries</h3>
              </div>
              <div className="p-6">
                {customer.deliveries?.length === 0 ? (
                  <p className="text-gray-500 text-center py-8">No deliveries yet</p>
                ) : (
                  <div className="space-y-3">
                    {customer.deliveries?.slice(0, 10).map((delivery: any) => (
                      <div key={delivery.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                        <div>
                          <p className="font-medium text-gray-900">{formatDate(delivery.deliveryDate)}</p>
                          <p className="text-sm text-gray-500">{formatQuantity(delivery.quantityMl)}</p>
                        </div>
                        <span className={`inline-flex items-center px-2.5 py-1 rounded-full text-xs font-semibold ${
                          delivery.status === "DELIVERED" 
                            ? "bg-emerald-100 text-emerald-800"
                            : delivery.status === "PAUSED"
                            ? "bg-amber-100 text-amber-800"
                            : "bg-gray-100 text-gray-800"
                        }`}>
                          {DELIVERY_STATUS_LABELS[delivery.status as keyof typeof DELIVERY_STATUS_LABELS]}
                        </span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>

          {/* Right Column - Sidebar */}
          <div className="space-y-6">
            
            {/* Wallet */}
            <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
              <div className="px-6 py-5 border-b border-gray-200 flex items-center justify-between">
                <h3 className="text-lg font-semibold text-gray-900">Wallet</h3>
                <button
                  onClick={() => setShowWalletAdjust(true)}
                  className="px-3 py-1.5 text-sm font-medium text-gray-700 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
                >
                  Adjust
                </button>
              </div>
              <div className="p-6">
                <p className={`text-4xl font-bold ${
                  (customer.wallet?.balancePaise || 0) < 0 ? 'text-red-600' : 'text-gray-900'
                }`}>
                  {formatPaise(customer.wallet?.balancePaise || 0)}
                </p>

                {showWalletAdjust && (
                  <div className="mt-6 pt-6 border-t border-gray-200 space-y-4">
                    <div className="flex gap-2 p-1 bg-gray-100 rounded-xl">
                      <button
                        onClick={() => setWalletType("ADMIN_CREDIT")}
                        className={`flex-1 flex items-center justify-center gap-1.5 py-2 rounded-lg text-sm font-medium transition-colors ${
                          walletType === "ADMIN_CREDIT"
                            ? "bg-white text-emerald-700 shadow-sm"
                            : "text-gray-600"
                        }`}
                      >
                        <Plus className="w-4 h-4" />
                        Credit
                      </button>
                      <button
                        onClick={() => setWalletType("ADMIN_DEBIT")}
                        className={`flex-1 flex items-center justify-center gap-1.5 py-2 rounded-lg text-sm font-medium transition-colors ${
                          walletType === "ADMIN_DEBIT"
                            ? "bg-white text-red-700 shadow-sm"
                            : "text-gray-600"
                        }`}
                      >
                        <Minus className="w-4 h-4" />
                        Debit
                      </button>
                    </div>
                    <input
                      type="number"
                      placeholder="Amount in rupees"
                      value={walletAmount}
                      onChange={(e) => setWalletAmount(e.target.value)}
                      className="w-full h-10 px-3 border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                    />
                    <input
                      type="text"
                      placeholder="Reason/Description"
                      value={walletDescription}
                      onChange={(e) => setWalletDescription(e.target.value)}
                      className="w-full h-10 px-3 border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-emerald-500"
                    />
                    <div className="flex gap-2">
                      <button
                        onClick={() => walletMutation.mutate()}
                        disabled={!walletAmount || !walletDescription || walletMutation.isPending}
                        className="flex-1 h-10 px-4 bg-slate-900 text-white rounded-lg text-sm font-medium hover:bg-slate-800 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                      >
                        {walletMutation.isPending ? "Saving..." : "Save"}
                      </button>
                      <button
                        onClick={() => setShowWalletAdjust(false)}
                        className="h-10 px-4 text-sm text-gray-600 hover:bg-gray-100 rounded-lg transition-colors"
                      >
                        Cancel
                      </button>
                    </div>
                  </div>
                )}

                {/* Recent Transactions */}
                {customer.wallet?.transactions?.length > 0 && !showWalletAdjust && (
                  <div className="mt-6 pt-6 border-t border-gray-200">
                    <p className="text-sm font-medium text-gray-700 mb-3">Recent</p>
                    <div className="space-y-2">
                      {customer.wallet.transactions.slice(0, 5).map((tx: any) => (
                        <div key={tx.id} className="flex items-center justify-between text-sm">
                          <span className="text-gray-600 truncate flex-1 mr-3">{tx.description}</span>
                          <span className={`font-semibold ${tx.amountPaise > 0 ? "text-emerald-600" : "text-red-600"}`}>
                            {tx.amountPaise > 0 ? "+" : ""}{formatPaise(tx.amountPaise)}
                          </span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* Bottle Balance */}
            <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
              <div className="px-6 py-5 border-b border-gray-200">
                <h3 className="text-lg font-semibold text-gray-900">Bottles with Customer</h3>
              </div>
              <div className="p-6 space-y-3">
                <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <span className="text-gray-600">1L Bottles</span>
                  <span className="text-2xl font-bold text-gray-900">{bottleBalance?.large || 0}</span>
                </div>
                <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <span className="text-gray-600">500ml Bottles</span>
                  <span className="text-2xl font-bold text-gray-900">{bottleBalance?.small || 0}</span>
                </div>
              </div>
            </div>

            {/* Account Info */}
            <div className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden">
              <div className="px-6 py-5 border-b border-gray-200">
                <h3 className="text-lg font-semibold text-gray-900">Account Info</h3>
              </div>
              <div className="p-6 space-y-3 text-sm">
                <div className="flex justify-between">
                  <span className="text-gray-500">Created</span>
                  <span className="text-gray-900 font-medium">{formatDate(customer.createdAt)}</span>
                </div>
                {customer.approvedAt && (
                  <div className="flex justify-between">
                    <span className="text-gray-500">Approved</span>
                    <span className="text-gray-900 font-medium">{formatDate(customer.approvedAt)}</span>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  )
}
